html, body {
  background-color: #333; }

body {
  background-color: #ffffff;
  color: #000000;
  font-size: 16px;
  line-height: 1.5;
  font-family: "Helvetica, Arial, sans-serif"; }

@media screen and (min-width: 30em) {
  body {
    font-family: "AvenirNextCondensed", Arial, sans-serif;
    margin: 0 auto;
    width: 30em; } }

h1, h2 {
  font-family: "TwCenMT-Regular", ArialMT, Arial, sans-serif; }

h3, h4, h5, h6 {
  font-family: "TwCenMT-Bold", Arial-BoldMT, Arial, sans-serif; }

ul, li {
  list-style-type: none; }

a:active, a:hover, a:focus {
  color: #eb018c;
  text-decoration: none; }

@media screen and (min-width: 30em) {
  a:visited {
    outline: none; } }

.footer {
  background: #9b9fa2;
  color: #ffffff;
  padding: 10px 30px;
  overflow: auto; }

.header {
  background-color: #03b8ad;
  font-size: 14px;
  padding: 10px 15px 0;
  overflow: auto;
  position: relative; }

.banner {
  overflow: auto; }
  .banner__image {
    display: block;
    width: 100%; }

.call-to-action {
  padding: 15px 10px; }
  @media screen and (min-width: 30em) {
    .call-to-action__item--with-icon {
      text-indent: -99em;
      position: absolute;
      right: 5px;
      top: 0; } }
  .call-to-action__item--with-icon-search {
    right: 30px; }
  .call-to-action__item--with-icon-search_footer {
    right: 20px; }
  .call-to-action__nav-item-text--backToTop {
    color: #ffffff; }

button,
[type="button"],
[type="reset"],
[type="submit"],
.call-to-action__item {
  border: none;
  border-radius: 2px;
  color: #ffffff;
  cursor: pointer;
  font-size: 16px;
  margin: 10px 0;
  padding: 10px;
  text-align: center;
  text-transform: uppercase; }

.copyright {
  background: #03b8ad;
  padding: 10px 30px; }
  .copyright .call-to-action--backToTop {
    background: #5bc5bb;
    border-radius: 5px;
    padding: 20px;
    width: 45%;
    margin: 15px 0;
    text-align: center; }
  .copyright__disclaimer {
    color: #ffffff;
    margin: 0;
    font-size: 12px; }

input[type="text"],
input[type="number"],
input[type="password"],
input[type="email"],
input[type="tel"],
input[type="search"],
input[type="week"],
input[type="date"],
input[type="month"],
input[type="datetime"],
input[type="datetime-local"],
input[type="url"],
select {
  background-color: #ffffff;
  border: none;
  color: #03b8ad;
  font-size: 18px;
  padding: 10px;
  width: 100%; }

input[type="search"] {
  -webkit-appearance: textfield;
  display: block;
  margin: 0 auto;
  width: 85%; }

fieldset {
  border: none;
  margin: 0; }

.languages {
  float: right;
  text-transform: uppercase; }
  .languages__anchor {
    color: #ffffff;
    display: inline-block;
    padding-left: 5px; }
  .languages .active {
    color: #ed018c;
    text-decoration: none; }
  .languages--footer {
    float: none;
    padding: 5px; }
    .languages--footer p {
      margin: 0;
      display: inline-block; }

.logo-wrapper {
  display: inline-block; }

.logo {
  color: #ffffff;
  display: block;
  font-size: 12px;
  padding: 5px;
  text-transform: uppercase;
  vertical-align: middle; }
  .logo img {
    max-width: 100%;
    height: auto;
    width: 120px; }
  .logo--left {
    text-align: left; }

.menu {
  display: block; }
  .menu-list {
    padding: 0;
    margin: 0;
    text-align: center; }
    .menu-list__label {
      color: #ffffff;
      font-size: 18px; }
    .menu-list__item {
      display: block;
      padding: 10px 5px;
      text-transform: uppercase; }
    .menu-list__anchor {
      color: #ffffff;
      font-size: 14px; }
    .menu-list--sections {
      margin: 0; }
  .menu--header {
    background-color: #ffffff;
    display: none; }
  .menu--footer {
    display: inline-block; }
    .menu--footer .menu-list {
      text-align: left; }
      .menu--footer .menu-list__item {
        text-transform: uppercase;
        padding: 5px; }
        .menu--footer .menu-list__item [type="submit"] {
          background: transparent;
          border-radius: 0;
          padding: 0;
          margin: 0; }
          .menu--footer .menu-list__item [type="submit"]:active, .menu--footer .menu-list__item [type="submit"]:hover, .menu--footer .menu-list__item [type="submit"]:focus {
            color: #eb018c;
            text-decoration: none;
            outline: none; }
          .menu--footer .menu-list__item [type="submit"]:visited {
            outline: none; }
      .menu--footer .menu-list__anchor {
        text-transform: inherit; }

.nav {
  clear: both;
  overflow: auto; }
  .nav-list {
    padding: 0;
    margin: 0;
    float: left; }
    .nav-list__item {
      display: inline-block; }
    .nav-list__anchor {
      color: #ffffff;
      padding: 5px;
      text-transform: uppercase; }
    .nav-list__label {
      color: #ffffff;
      text-transform: uppercase;
      padding: 5px 0 5px 5px; }
      .nav-list__label:before {
        content: '';
        background: url("/static/img/icons/rwanda/dropdown-menu.png") no-repeat 0 0/100%;
        width: 25px;
        height: 25px;
        display: inline-block;
        vertical-align: text-top;
        margin: 0 10px 0 0; }

.search {
  padding-bottom: 5px; }
  .search__form {
    position: relative; }
  .search label {
    color: #ffffff;
    cursor: pointer;
    display: block;
    text-transform: uppercase;
    position: absolute;
    right: 5px;
    top: 13px; }
    @media screen and (min-width: 30em) {
      .search label {
        background-image: url("/static/img/icons/rwanda/search-icon.png"), url("/static/img/icons/rwanda/search-icon.svg");
        background-repeat: no-repeat;
        background-position: 0 0;
        background-size: 88%;
        height: auto;
        text-indent: -1000px;
        width: 30px; } }
  .search .search__input {
    visibility: hidden;
    position: absolute; }
    @media screen and (min-width: 30em) {
      .search .search__input {
        right: 5px;
        top: 13px; } }
  .search .search__input + * {
    display: none; }
  .search .search__input:checked + * {
    display: block; }
  .search button,
  .search [type="button"],
  .search [type="reset"],
  .search [type="submit"] {
    background-color: transparent;
    background-image: url("/static/img/icons/rwanda/search-icon-submit.svg");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 60%;
    margin: 0;
    padding: 11px 0;
    height: auto;
    width: 50px; }

.footer .search input[type="search"] {
  width: 90%; }

.social-media {
  background: url("/static/img/lines/rwanda/pattern-social.png") repeat center center/100%;
  padding: 15px 20px;
  width: 100%;
  overflow: auto; }
  .social-media .heading__micro {
    color: #000000;
    font-size: 20px; }
  .social-media-list {
    margin: 0;
    padding: 0; }
    .social-media-list__item {
      padding: 5px; }
  .social-media__icon {
    vertical-align: middle; }
  .social-media__label {
    color: #000000;
    font-size: 18px;
    padding: 0 0 0 10px; }
